import React from "react";
import { useInView } from "react-intersection-observer";
import jf from "../../assets/about/jackfrost_p3r.png";

const About = () => {
  const [ref, inView] = useInView({ threshold: 0.3, triggerOnce: true });

  return (
    <div
      id="about"
      className="size-full bg-black flex flex-col items-center justify-center p-4 lg:p-10 gap-4 text-white overflow-hidden"
    >
      <h1 ref={ref} className="text-3xl text-red-800">
        Welcome!
      </h1>
      <div className="relative h-[500px] w-[280px] md:w-[420px] lg:w-[500px] perspective my-10">
        <div
          className={`absolute size-full transition-transform duration-700 transform-style preserve-3d ${
            inView ? "animate-flip-side-3d" : "opacity-0"
          }`}
        >
          <div className="absolute size-full backface-hidden p-8 gap-4 flex flex-col items-center justify-center text-center bg-red-800 rounded-lg text-sm lg:text-base">
            <p className="text-lg">Hope you're having an amazing day!</p>
            <p>I am a software engineer by profession from Pakistan.</p>
            <p>I have worked on different technologies</p>
            <p>
              In my free time I like to play games and explore other
              technologies that I am interested in.
            </p>
            <p>
              I also used to crack games and consoles in my free time before
              geting a full-time job.
            </p>
            <p>
              Feel free to explore my portfolio and drop a message or an email
              if there is anything you wish to know or if you want to connect.
            </p>
            <p>Looking forward to connecting and exploring opportunities.</p>
          </div>
          <div className="absolute size-full backface-hidden bg-red-800 rotate-y-180 rounded-lg flex items-center justify-center">
            <img
              src={jf}
              height={350}
              width={350}
              alt="welcome"
              className="size-[240px] md:size-[350px] object-cover"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
