import React from "react";
import { useInView } from "react-intersection-observer";
import { contact } from "../../supporting files/contact";
import "./css/contact.css";

const Contact = () => {
  const [ref, inView] = useInView({ threshold: 0.3, triggerOnce: true });

  return (
    <div
      id="contact"
      className="flex flex-col items-center justify-center p-4 lg:p-[75px] gap-4 bg-black text-white"
    >
      <h1 ref={ref} className="text-4xl font-bold text-red-800 text-center">
        Let's Connect
      </h1>
      <div className="flex flex-wrap w-full p-8 items-center justify-center gap-4 mt-8">
        {contact.map((c, i) => (
          <div
            key={i}
            className={`flex flex-wrap items-center justify-center md:justify-normal w-full max-w-[420px] rounded-xl bg-yellow-800 p-4 gap-4 opacity-0
                ${inView && "slide-in"}`}
            style={{ "--sd": `${i * 100}ms` }}
          >
            <div className="size-16 rounded-full bg-black p-3 flex items-center justify-center">
              <span className="size-full text-yellow-500">{c.icon}</span>
            </div>
            <h1 className="text-xs md:text-base">{c.info}</h1>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Contact;
