import React from "react";
import { useInView } from "react-intersection-observer";
import { external_link_svg } from "../../assets/svgs";
import { hobby_projects } from "../../supporting files/projects";
import "./css/hobby_projects.css";

const HobyProjects = () => {
  const [ref, inView] = useInView({ threshold: 0.3, triggerOnce: true });

  return (
    <div
      id="hobby"
      className="flex flex-col items-center justify-center p-4 gap-4 lg:p-[75px] bg-black text-white"
    >
      <h1 ref={ref} className="text-4xl font-bold text-red-800 text-center">
        Hobby Projects
      </h1>
      <div className="flex flex-wrap w-full max-w-[1400px] items-center justify-center gap-4 mt-8">
        {hobby_projects.map((p, i) => (
          <div
            key={i}
            className={`w-full max-w-[550px] scale-0 group 
                ${inView && "scale-up-h"}`}
            style={{ "--hd": `${i * 100}ms` }}
          >
            <div className="relative flex flex-wrap size-full items-center justify-center gap-4 p-4 rounded-xl bg-gray-700 duration-300 group-hover:scale-[1.02]">
              {p.link && (
                <a
                  href={p.link}
                  title={p.title}
                  rel="noopener noreferrer"
                  target="_blank"
                  className="absolute top-2 right-2 size-7 p-2 rounded-full bg-red-800 text-white flex items-center justify-center duration-300 hover:bg-gray-900 hover:text-red-800 hover:scale-[1.04]"
                >
                  <span className="size-full">{external_link_svg}</span>
                </a>
              )}
              <span className="size-[150px] self-center">{p.asset}</span>
              <div className="flex flex-col size-full max-w-[300px] justify-center gap-4">
                <h2 className="text-lg font-semibold">{p.title}</h2>
                <div className="flex flex-wrap w-full gap-2">
                  {p.tags.map((t, i) => (
                    <div
                      key={i}
                      className={`flex px-2 py-1 text-xs bg-red-800 rounded-md pointer-events-none`}
                    >
                      {t.name}
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default HobyProjects;
