import React from "react";
import { useInView } from "react-intersection-observer";
import { education } from "../../supporting files/education";

const Education = () => {
  const [ref, inView] = useInView({ threshold: 0.3, triggerOnce: true });

  return (
    <div
      id="education"
      className="p-4 lg:p-[75px] flex flex-col items-center justify-center gap-4 bg-black"
    >
      <h1 ref={ref} className="text-4xl font-bold text-red-800 text-center">
        Education
      </h1>
      <div className="flex flex-wrap w-full max-w-[1400px] items-center justify-center gap-4 mt-3">
        {education.map((e, index) => (
          <div
            key={index}
            className={` ${
              inView &&
              `first:animate-slide-in-300 last:animate-slide-in-700 opacity-0 group w-full max-w-[400px] ${
                index === 1 && "animate-slide-in-500"
              }`
            }`}
          >
            <div className="flex flex-col w-full h-full min-h-[250px] items-center text-center text-white text-lg p-4 gap-4 rounded-xl bg-gray-700 duration-300 group-hover:scale-[1.04]">
              <span className="size-[40px]">{e.icon}</span>
              <h1 className="text-xl font-semibold">{e.degree}</h1>
              <h2 className="font-semibold">{e.duraton}</h2>
              <h2>{e.institute}</h2>
              {e.result && <h3>{e.result}</h3>}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Education;
