import React from "react";
import { useInView } from "react-intersection-observer";
import { external_link_svg } from "../../assets/svgs";
import { volunteering } from "../../supporting files/volunteering";

const Volunteering = () => {
  const [ref, inView] = useInView({ threshold: 0.3, triggerOnce: true });

  return (
    <div
      id="volunteering"
      className="flex flex-col items-center justify-center p-4 lg:p-[75px] gap-4 bg-black text-white"
    >
      <h1 ref={ref} className="text-4xl font-bold text-red-800 text-center">
        Volunteer Work
      </h1>
      <div className="flex flex-wrap items-center justify-center gap-4 w-full max-w-[1200px] mt-8">
        {volunteering.map((v, i) => (
          <div
            key={i}
            className={`w-full max-w-[400px] opacity-0 group ${
              inView && "animate-slide-in-300"
            }`}
          >
            <div className="flex flex-col size-full p-4 gap-2 rounded-xl bg-gray-700 duration-300 group-hover:scale-[1.04]">
              {v.link && (
                <a
                  href={v.link}
                  title={v.lt}
                  rel="noopener noreferrer"
                  target="_blank"
                  className="absolute top-4 right-4 size-7 p-2 rounded-full bg-red-800 text-white flex items-center justify-center duration-300 hover:bg-gray-900 hover:text-red-800 hover:scale-[1.04]"
                >
                  <span className="size-full">{external_link_svg}</span>
                </a>
              )}
              <h1 className="text-lg font-semibold">{v.location}</h1>
              <img
                src={v.asset}
                alt={v.title}
                className="w-full h-[200px] object-contain"
              />
              <h2 className="font-semibold">{v.post}</h2>
              <h3 className="text-sm">{v.duration}</h3>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Volunteering;
