import React from "react";
import About from "../components/home/About";
import Contact from "../components/home/Contact";
import Education from "../components/home/Education";
import HomeHero from "../components/home/Hero";
import HobyProjects from "../components/home/Hobby_Projects";
import Menu from "../components/home/MenuDropDown";
import Projects from "../components/home/Projects";
import ToolsTechs from "../components/home/Tools_Technologies";
import Volunteering from "../components/home/Volunteering";
import Work from "../components/home/Work";

const Home = () => {
  return (
    <div className="relative">
      <Menu />
      <HomeHero />
      <About />
      <Education />
      <Work />
      <Projects />
      <HobyProjects />
      <Volunteering />
      <ToolsTechs />
      <Contact />
      <footer className="flex items-center justify-center text-center p-1 text-white bg-black text-xs/8">
        The "Jack Frost" emoticon is the property of ATLUS
      </footer>
    </div>
  );
};

export default Home;
