import React from "react";
import {
  VerticalTimeline,
  VerticalTimelineElement,
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import { work } from "../../supporting files/work";
import "./css/custom_timeline.css";

const Work = () => {
  return (
    <div
      id="work"
      className="size-full relative bg-black flex flex-col items-center justify-center gap-4 p-4 lg:p-[75px]"
    >
      <h1 className="text-4xl font-bold text-red-800 text-center">
        Work History
      </h1>
      <VerticalTimeline>
        {work.map((w, index) => (
          <VerticalTimelineElement
            key={index}
            className="relative vertical-timeline-element--work"
            contentStyle={{
              background: `#991b1b`, // ${index % 2 === 0 ? "#aa002d" : "#001220"}
              // backgroundImage: `url(${feature.img})`,
              // backgroundPosition: "top",
              // backgroundSize: "cover",
              // backgroundRepeat: "no-repeat",
              // color: "#fff",
              // padding: "20px",
            }}
            contentArrowStyle={{
              borderRight: `10px solid #991b1b`, // ${index % 2 === 0 ? "#aa002d" : "#001220"}
            }}
            iconStyle={{
              background: "#991b1b", // ${index % 2 === 0 ? "#aa002d" : "#001220"}
              color: "#fff",
            }}
            // icon={Icon(feature.Icon)}
          >
            <div className="flex flex-col gap-4 p-2 items-center justify-center text-white  ">
              <h3 className="text-xl font-semibold text-center">{w.title}</h3>
              <h3 className="text-xl font-semibold text-center text-white">
                {w.location}
              </h3>
              <p className="text-lg font-medium">{w.duration}</p>
              {w.description}
            </div>
          </VerticalTimelineElement>
        ))}
      </VerticalTimeline>
    </div>
  );
};

export default Work;
