import React, { useState } from "react";
import back from "../../assets/hero/hero-back.png";
import front from "../../assets/hero/hero-front.png";
import { flip_svg } from "../../assets/svgs";
import "./css/hero.css";

const HomeHero = () => {
  const gridL = 6;
  const gridM = 4;

  const [flip, setFlip] = useState(false);
  const [animate, setAnimate] = useState(false);
  const cells = Array(gridL * gridL).fill(true);
  const cellsM = Array(gridM * gridM).fill(true);

  const handleClick = () => {
    setAnimate(true);
    setFlip((prevState) => !prevState);
  };

  return (
    <div id="hero" className="relative z-0 overflow-hidden">
      <h1 className="absolute -z-10 text-black">
        Welcome to my Portfolio Website. I will be updating it semi-regularly.
      </h1>

      <button
        id="btn"
        name="flip cards"
        className={`hidden md:flex absolute z-10 bottom-4 right-4 bg-white size-20 rounded-full p-1 overflow-hidden
            ${animate ? "animate-click" : "animate-stagger"} `}
        onClick={() => handleClick()}
        onAnimationEnd={() => setAnimate(false)}
      >
        <div
          className={`size-full duration-300 rounded-full p-4
            ${flip ? "bg-gray-800" : "bg-red-800"}`}
        >
          <span className="size-full text-white">{flip_svg}</span>
        </div>
      </button>

      <div className="hidden lg:grid grid-cols-6 grid-rows-6 h-screen w-full bg-black p-2 gap-1">
        {cells.map((_, index) => {
          const row = Math.floor(index / gridL);
          const col = index % gridL;

          return (
            <div
              key={index}
              className={`group perspective rounded-md overflow-hidden scale-0 scale-up`}
              style={{ "--d": `${index * 12}ms` }}
            >
              <div
                className={`relative size-full transition-transform duration-500 transform-style group-hover:rotate-x-180 ${
                  flip ? "rotate-x-180" : ""
                }`}
              >
                {/* Front */}
                <div
                  className="absolute inset-0 bg-gray-800 flex justify-center items-center backface-hidden"
                  style={{
                    backgroundImage: `url(${front})`,
                    backgroundSize: `${gridL * 100}% ${gridL * 100}%`,
                    backgroundPosition: `${(col / (gridL - 1)) * 100}% ${
                      (row / (gridL - 1)) * 100
                    }%`,
                  }}
                ></div>
                {/* Back */}
                <div
                  className="absolute inset-0 bg-red-800 flex justify-center items-center rotate-x-180 backface-hidden"
                  style={{
                    backgroundImage: `url(${back})`,
                    backgroundSize: `${gridL * 100}% ${gridL * 100}%`,
                    backgroundPosition: `${(col / (gridL - 1)) * 100}% ${
                      (row / (gridL - 1)) * 100
                    }%`,
                  }}
                ></div>
              </div>
            </div>
          );
        })}
      </div>
      <div className="hidden md:grid lg:hidden grid-cols-4 grid-rows-4 h-screen w-full bg-black p-2 gap-1">
        {cellsM.map((_, index) => {
          const row = Math.floor(index / gridM);
          const col = index % gridM;

          return (
            <div
              key={index}
              className={`group perspective rounded-md overflow-hidden scale-0 scale-up`}
              style={{ "--d": `${index * 12}ms` }}
            >
              <div
                className={`relative size-full transition-transform duration-500 transform-style group-hover:rotate-x-180 ${
                  flip ? "rotate-x-180" : ""
                }`}
              >
                {/* Front */}
                <div
                  className="absolute inset-0 bg-gray-800 flex justify-center items-center backface-hidden"
                  style={{
                    backgroundImage: `url(${front})`,
                    backgroundSize: `${gridM * 100}% ${gridM * 100}%`,
                    backgroundPosition: `${(col / (gridM - 1)) * 100}% ${
                      (row / (gridM - 1)) * 100
                    }%`,
                  }}
                ></div>
                {/* Back */}
                <div
                  className="absolute inset-0 bg-red-800 flex justify-center items-center rotate-x-180 backface-hidden"
                  style={{
                    backgroundImage: `url(${back})`,
                    backgroundSize: `${gridM * 100}% ${gridM * 100}%`,
                    backgroundPosition: `${(col / (gridM - 1)) * 100}% ${
                      (row / (gridM - 1)) * 100
                    }%`,
                  }}
                ></div>
              </div>
            </div>
          );
        })}
      </div>
      <div className="flex flex-col md:hidden p-4 bg-black h-screen items-center justify-center text-white gap-4">
        <h1 className="text-4xl font-semibold opacity-0 animate-slide-in-300">
          Abdul Rehman
        </h1>
        <h2 className="text-2xl font-medium opacity-0 animate-slide-in-500">
          Software Engineer
        </h2>
        <h2 className="opacity-0 animate-slide-in-700 bg-red-800 px-2 py-1 rounded-md">
          Tech Enthusiast
        </h2>
        <h2 className="opacity-0 animate-slide-in-1100 bg-red-800 px-2 py-1 rounded-md">
          Developer
        </h2>
        <h2 className="opacity-0 animate-slide-in-900 bg-red-800 px-2 py-1 rounded-md">
          Gamer
        </h2>
      </div>
    </div>
  );
};

export default HomeHero;
